import { DefaultApolloClient } from '@speedlo/graphql'
import debug from 'debug'
import { getPath, getRoot, getType, types } from 'mobx-state-tree'

import { TRootModel } from './RootModel'

export const BaseModel = types
  .model()
  .volatile(() => ({
    client: (null as unknown) as DefaultApolloClient,
  }))
  .actions(self => ({
    setClient(client: DefaultApolloClient) {
      self.client = client
    },
  }))
  .views(self => ({
    get log() {
      const name = getPath(self).trim() || getType(self).name
      return debug(`c2e:model/${name}`)
    },
    get root() {
      return getRoot<TRootModel>(self)
    },
  }))
