import { autorun } from 'mobx'
import React from 'react'

import { useRoot } from '../app/useRoot'
import { appConfig } from '../config'

export const PageTitle: React.FC = () => {
  const { business } = useRoot()

  React.useEffect(
    () =>
      autorun(() => {
        if (business.isValid) {
          document.title = `${business.branch!.brand.name} - ${
            appConfig.pageTitleSuffix
          }`
        } else {
          document.title = appConfig.pageTitleSuffix
        }
      }),
    [],
  )

  return null
}
