import { Catalog, setupI18n } from '@lingui/core'
import { I18nProvider } from '@lingui/react'
import { autorun } from 'mobx'
import React from 'react'

import { useRoot } from './app/useRoot'
import { logLang } from './helpers/logger'

interface IProps {
  rootInit: Promise<void>
}

export const i18n = setupI18n()

export const I18nManager: React.FC<IProps> = ({ rootInit, children }) => {
  const { user } = useRoot()

  React.useEffect(
    () =>
      autorun(async () => {
        if (!user.languageActivated) {
          await rootInit
          await loadCatalog(user.language)
          i18n.activate(user.language)
          user.activateLanguage()
          logLang('changed to %s', user.language)
        }
      }),
    [],
  )

  return <I18nProvider i18n={i18n}>{children}</I18nProvider>
}

async function loadCatalog(lang: string) {
  let catalog: Catalog
  if (process.env.NODE_ENV !== 'production') {
    // prettier-ignore
    catalog = await import(
      /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
      `@lingui/loader!./locale/${lang}/messages.po`
    )
  } else {
    // prettier-ignore
    catalog = await import(
      /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
      `./locale/${lang}/messages.js`
    )
  }
  i18n.load({ [lang]: catalog })
  logLang('loaded catalog %s', lang)
}
